import React from 'react';
import ReactDOM from 'react-dom';
import HeaderAboutMe from './components/HeaderAboutMe';
import SectionAboutMe from './components/SectionAboutMe';
import UpdateDetails from './components/UpdateDetails';
import SkillBox from "./components/SkillBox";
import LoadingScreen from "./components/LoadingScreen";
import './css/style.css';
import './css/icofont.min.css';
import App from './App';
import Skill from './components/Skill';
import base from "./base";
import * as serviceWorker from './serviceWorker';

var HeaderAboutMeisLoading = true;
var SectionAboutMeisLoading = true;
var SkillboxisLoading = true;
var loadingRemoved = false;

ReactDOM.render(<LoadingScreen/>,
  document.getElementById('loading-screen')
);

function handleChangeHeaderAboutMe(event) {
  if(event != null) {
    HeaderAboutMeisLoading = false;
  }

  if(HeaderAboutMeisLoading==false && SectionAboutMeisLoading==false && SkillboxisLoading==false && loadingRemoved==false) {
    loadingRemoved = true;
    ReactDOM.render(null, document.getElementById('loading-screen'));
    document.getElementsByClassName('header-heading')[0].style.animation = "moveInBottom 1s forwards";
    document.getElementsByClassName('header-about-me')[0].style.animation = "moveInBottom 1s 0.75s forwards";
  }
}

function handleChangeSectionAboutMe(event) {
  if(event != null) {
    SectionAboutMeisLoading = false;
    //document.getElementsByClassName('section-about-me')[0].style.height = "auto";
  }

  if(HeaderAboutMeisLoading==false && SectionAboutMeisLoading==false && SkillboxisLoading==false && loadingRemoved==false) {
    loadingRemoved = true;
    ReactDOM.render(null, document.getElementById('loading-screen'));
    document.getElementsByClassName('header-heading')[0].style.animation = "moveInBottom 1s forwards";
    document.getElementsByClassName('header-about-me')[0].style.animation = "moveInBottom 1s 0.75s forwards";
  }
}

function handleChangeSkillbox(event) {
  if(event != null) {
    SkillboxisLoading = false;
  }

  if(HeaderAboutMeisLoading==false && SectionAboutMeisLoading==false && SkillboxisLoading==false && loadingRemoved==false) {
    loadingRemoved = true;
    ReactDOM.render(null, document.getElementById('loading-screen'));
    document.getElementsByClassName('header-heading')[0].style.animation = "moveInBottom 1s forwards";
    document.getElementsByClassName('header-about-me')[0].style.animation = "moveInBottom 1s 0.75s forwards";
  }
}

function handleUpdateDetailsClick(event) {
  event.preventDefault();
  if(document.getElementById('section-update-details').innerHTML === "") {
    ReactDOM.render(<div className="update-details-main">
        <button  className="update-details-main-close-button" onClick={handleUpdateDetailsClick}>&#10005;</button>
        <UpdateDetails />
      </div>, document.getElementById('section-update-details'));
  }
  else {
    ReactDOM.render(null, document.getElementById('section-update-details'));
  }
}

ReactDOM.render(<HeaderAboutMe onChange={handleChangeHeaderAboutMe}/>,
  document.getElementById('header-about-me-react')
);

ReactDOM.render(<SectionAboutMe onChange={handleChangeSectionAboutMe}/>,
  document.getElementById('section-about-me-react')
);
/*
ReactDOM.render(<UpdateDetails />,
document.getElementById('root')
);
*/

ReactDOM.render(<SkillBox onChange={handleChangeSkillbox}/>,
  document.getElementById('section-skills-skillbox')
);

ReactDOM.render(<button className="footer-content-button" onClick={handleUpdateDetailsClick}>Update Details</button>,
  document.getElementsByClassName('footer-content-button-section')[0]
);

