window.onscroll = function() {
  if(document.body.scrollTop > ( window.innerHeight / 4 ) || document.documentElement.scrollTop > ( window.innerHeight / 4 ) ) {
    //alert("50 done");
    document.getElementsByClassName('section-about-me-title')[0].style.animation = "moveInBottom 1s forwards";
    document.getElementsByClassName('section-about-me-text')[0].style.animation = "moveInBottom 1s forwards 0.75s";
    document.getElementsByClassName('section-about-me-imgbox')[0].style.animation = "moveInBottom 1s forwards 0.75s";
    //document.getElementsByClassName('section-skills-title')[0].style.animation = "moveInBottom 1s forwards 0.75s";
  }
  else {
    document.getElementsByClassName('section-about-me-title')[0].style.animation = "moveOutTop 1s forwards";
    document.getElementsByClassName('section-about-me-text')[0].style.animation = "moveOutTop 1s forwards 0.75s";
    document.getElementsByClassName('section-about-me-imgbox')[0].style.animation = "moveOutTop 1s forwards 0.75s";
    //document.getElementsByClassName('section-skills-title')[0].style.animation = "moveOutTop 1s forwards 0.75s";
  }

  if(document.body.scrollTop > ( window.innerHeight) * 1.25 || document.documentElement.scrollTop > ( window.innerHeight) * 1.25 ) {
    //alert("50 done");
    document.getElementsByClassName('section-skills-title')[0].style.animation = "moveInBottom 1s forwards";
    document.getElementsByClassName('section-skills-skillbox')[0].style.animation = "moveInBottom 1s forwards 0.75";

  }
  else {
    document.getElementsByClassName('section-skills-title')[0].style.animation = "moveOutTop 1s forwards";
    document.getElementsByClassName('section-skills-skillbox')[0].style.animation = "moveOutTop 1s forwards 0.75";
    //document.getElementsByClassName('section-skills-skill')[0].style.animation = "moveInBottom 1s forwards 0.75s";
  }


}

